import request from '@/api/http.js'

// 获取配置数据
export const getLossConfig = async params => {
  return await request.post(`/activity.bet_loss/config`, params)
}

// 领取奖励
export const getLossReceive = async params => {
  return await request.post(`/activity.bet_loss/receive`, params)
}

// 返回记录
export const getLossRecord = async params => {
  return await request.post(`/activity.bet_loss/record`, params)
}
