<template>
  <div v-zinx class="comp loss_explain" v-if="state">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="loss_explain_comp">
      <div ref="lossExplainRef" class="loss_explain_box">
        <div class="loss_explain_title">
          <span class="title_text text_1">{{ title || $t('UI_Loss_Explain_Title') }}</span>
          <div class="title_close" @click="hide">
            <icon-font
              name="icon-close"
              :size="
                commonStyle(isApp ? 'loss_explain_title_close_icon_app_size' : 'loss_explain_title_close_icon_pc_size')
              "
              :dark="
                commonStyle(
                  isApp ? 'loss_explain_title_close_icon_app_color' : 'loss_explain_title_close_icon_pc_color'
                )
              "
            />
          </div>
        </div>
        <div class="loss_explain_content">
          <div class="content_text" v-html="content"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import _common from '@style/views/lossreturn.modules.scss'

export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    // 内容详情
    content: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState'])
  },
  watch: {
    // 如果弹窗清了，就要关闭
    popupState(n) {
      if (!n) {
        this.state = false
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 打开
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const lossExplainRef = this.$refs['lossExplainRef']
        if (lossExplainRef) this.$anim.insert(lossExplainRef)
      })
    },
    hide() {
      this.$nextTick(async () => {
        const lossExplainRef = this.$refs['lossExplainRef']
        if (lossExplainRef) this.$anim.close(lossExplainRef)

        setTimeout(() => {
          this.state = false
        }, POP_TIME)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/lossreturn.modules.scss';

