  user-select: none;
  .loss_explain_box {
    width: $loss_explain_comp_box_app_width;
    border-radius: $loss_explain_comp_box_app_radius;
    background: $loss_explain_comp_box_app_bg;
    .loss_explain_title {
      width: $loss_explain_comp_box_title_app_width;
      padding: $loss_explain_comp_box_title_app_padding;
      background: $loss_explain_comp_box_title_app_bg;
      position: relative;
      @include setBox(row, center, center);
      .title_text {
        font-size: $loss_explain_comp_box_title_text_app_size;
        font-weight: $loss_explain_comp_box_title_text_app_weight;
        color: $loss_explain_comp_box_title_text_app_color;
      }
      .title_close {
        font-size: 0;
        position: absolute;
        right: $loss_explain_comp_box_title_close_app_right;
        top: $loss_explain_comp_box_title_close_app_top;
        transform: $loss_explain_comp_box_title_close_app_transform;
        cursor: pointer;
      }
    }
    .loss_explain_content {
      width: $loss_explain_content_app_width;
      padding: $loss_explain_content_app_padding;
      .content_text {
        width: $loss_explain_content_text_app_width;
        max-height: $loss_explain_content_text_app_max_height;
        font-size: $loss_explain_content_text_app_size;
        font-weight: $loss_explain_content_text_app_weight;
        color: $loss_explain_content_text_app_color;
        line-height: $loss_explain_content_text_app_line;
        overflow-y: auto;
        word-break: break-all;
        white-space: pre-wrap;
      }
    }
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .loss_explain_comp {
    width: $loss_explain_comp_pc_width;
    left: $loss_explain_comp_pc_left;
    transform: $loss_explain_comp_pc_transform;
    .loss_explain_box {
      border-radius: $loss_explain_comp_box_pc_radius;
      .loss_explain_title {
        padding: $loss_explain_comp_box_title_pc_padding;
        .title_close {
          right: $loss_explain_comp_box_title_close_pc_right;
        }
      }
      .loss_explain_content {
        padding: $loss_explain_comp_box_content_pc_padding;
      }
    }
  }
}
</style>
