<template>
  <div v-zinx class="comp loss_record" v-if="state">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="loss_record_comp">
      <div ref="lossrecordRef" class="loss_record_box">
        <div class="loss_record_title">
          <span class="title_text text_1">{{ $t('UI_Loss_Previous_Records_Title') }}</span>
          <div class="title_close" @click="hide">
            <icon-font name="icon-close" :size="commonStyle(isApp ? '16' : '18')" :dark="commonStyle('#fff')" />
          </div>
        </div>
        <div class="loss_record_content">
          <div class="table_header" v-if="list?.length">
            <div class="table_row_1">{{ $t('UI_Date') }}</div>
            <div class="table_row_2">{{ $t('UI_Loss_Reward_Amount') }}</div>
            <div class="table_row_3">{{ $t('UI.MakeMoney_5_Withdrawal_6') }}</div>
          </div>
          <div class="table_body">
            <div class="table_row" v-for="(item, index) in list" :key="index">
              <div class="table_row_1" v-html="item.time" />
              <div class="table_row_2">{{ `${currencySymbol}${item.send_gold}` }}</div>
              <div class="table_row_3">
                <div class="receive_btn" :style="{ color: item.color }">{{ item.text }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import _common from '@style/views/lossreturn.modules.scss'

export default {
  props: {
    // 档位列表
    list: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig']),
    ...mapState('dialog', ['popupState']),
    ...mapState('switchState', ['currencyShow']),
    // 金额标识
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    }
  },
  watch: {
    // 如果弹窗清了，就要关闭
    popupState(n) {
      if (!n) {
        this.state = false
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 打开
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const lossrecordRef = this.$refs['lossrecordRef']
        if (lossrecordRef) this.$anim.insert(lossrecordRef)
      })
    },
    hide() {
      this.$nextTick(async () => {
        const lossrecordRef = this.$refs['lossrecordRef']
        if (lossrecordRef) this.$anim.close(lossrecordRef)

        setTimeout(() => {
          this.state = false
        }, POP_TIME)
      })
    }
  }
}
</script>

