<template>
  <v-container ref="boxIds" class="lossreturn_container">
    <div class="page lossreturn">
      <div class="loss_title">
        <template v-if="isLossreActive">
          <div class="loss_time_box">
            <div class="loss_time_day">
              <div class="num">{{ lossreTimeData.days }}</div>
              <div class="text">{{ $t('UI_Loss_Time_Days') }}</div>
            </div>
            <span class="colon">&nbsp;:&nbsp;</span>
            <div class="loss_time_hours">
              <div class="num">{{ lossreTimeData.hours }}</div>
              <div class="text">{{ $t('UI_Loss_Time_Hours') }}</div>
            </div>
            <span class="colon">&nbsp;:&nbsp;</span>
            <div class="loss_time_min">
              <div class="num">{{ lossreTimeData.minutes }}</div>
              <div class="text">{{ $t('UI_Loss_Time_Min') }}</div>
            </div>
            <span class="colon">&nbsp;:&nbsp;</span>
            <div class="loss_time_sec">
              <div class="num">{{ lossreTimeData.seconds }}</div>
              <div class="text">{{ $t('UI_Loss_Time_Sec') }}</div>
            </div>
            <div class="finish_in">{{ $t('UI_Loss_Finish_In') }}</div>
          </div>
        </template>
        <template v-else>
          <div class="loss_time_end">{{ $t('UI_Loss_Has_Ended') }}</div>
        </template>
        <div class="loss_tip" @click="onExplain">
          <icon-font
            name="icon-loss_helper"
            :size="commonStyle(isApp ? 'lossreturn_loss_tip_icon_app_size' : 'lossreturn_loss_tip_icon_pc_size')"
            :dark="commonStyle(isApp ? 'lossreturn_loss_tip_icon_app_color' : 'lossreturn_loss_tip_icon_pc_color')"
          />
        </div>
        <div class="loss_record" @click="onRecord" v-if="isNewActivity && recordList?.length">
          <div
            v-html="recordSvg"
            :style="{
              width:
                commonStyle(isApp ? 'lossreturn_loss_tip_icon_app_size' : 'lossreturn_loss_tip_icon_pc_size') + 'px',
              height:
                commonStyle(isApp ? 'lossreturn_loss_tip_icon_app_size' : 'lossreturn_loss_tip_icon_pc_size') + 'px'
            }"
          ></div>
        </div>
      </div>
      <div class="loss_waves">
        <div class="loss_wave">
          <div class="loss_wave_box">
            <div class="loss_wave_bg">
              <div class="loss_wave_before"></div>
              <div class="loss_wave_after"></div>
              <div class="loss_wave_text">
                <span class="symbol">{{ currencySymbol }}&nbsp;&nbsp;</span>
                <span class="money">
                  <count-to
                    :startVal="0"
                    :endVal="maxMoney != -1 && Number(returnMoney) >= maxMoney ? maxMoney : Number(returnMoney)"
                    :duration="duration"
                    :decimals="2"
                  />
                </span>
              </div>
              <div class="loss_wave_max_text" v-if="maxMoney != -1 && Number(returnMoney) >= maxMoney">
                {{ $t('UI_Loss_Wave_Max_Text') }}
              </div>
            </div>
          </div>
          <div class="loss_shadow"></div>
        </div>
      </div>
      <div class="lower_limit">
        <span class="limit_money">
          {{ $t('UI_Loss_Minimum_Text', { money: `${currencySymbol} ${$u.formatNumber(minMoney || 0)}` }) }}
          <!-- <count-to :startVal="0" :endVal="" :duration="duration" :decimals="2" /> -->
        </span>
      </div>
      <div class="loss_reward">
        <div class="lt">
          <img class="lt_img" :src="loadIcon('loss_reward')" alt="" />
          <div class="lt_money">
            <div class="lt_title">
              <span class="money_symbol">{{ currencySymbol }}&nbsp;</span>
              <span class="money_text">
                <count-to :startVal="0" :endVal="Number(returnReward || 0)" :duration="duration" :decimals="2" />
              </span>
            </div>
            <div class="money_desc" v-if="isNewActivity">
              {{ $t('UI_Loss_Not_Receive_Gold') }}：<span>{{ currencySymbol }}&nbsp;</span>{{ notReceiveGold }}
            </div>
          </div>
        </div>
        <div class="rt">
          <div
            class="lossreturn_receive_btn btn_radius"
            :class="{ disabled_btn: Number(returnReward) <= 0 }"
            @click="onReceive"
            v-zoom
          >
            <auto-font
              uuid="lossTeceiveId"
              :maxFont="
                commonStyle(isApp ? 'lossreturn_receive_btn_app_max_size' : 'lossreturn_receive_btn_pc_max_size')
              "
              :level="2"
              :text="$t('UI_Loss_Receive_Text')"
            />
          </div>
        </div>
      </div>
      <div class="loss_refund" v-if="!isNewActivity">
        <img class="refund_level_img" :src="getCacheIcon('vip_crown')" alt="" />
        <div class="refund_level_box">
          <span class="refund_level_text">{{ $t('UI_VIP_Current_Livel') }}</span>
          <span class="refund_level_num">{{ getVipCurrentLevel(user.vip_id) }}</span>
        </div>
        <div class="refund_title">{{ $t('UI_Loss_Refund_Ratio_Text') }}</div>
        <div class="refund_list">
          <div class="refund_item_title">
            <span class="grade">{{ $t('UI_Loss_Refund_Grade') }}</span>
            <span class="percentage">{{ $t('UI_Loss_Refund_Percentage') }}</span>
          </div>
          <div
            class="refund_item"
            :class="{ current: item.vip_id == user.vip_id }"
            v-for="item in returnRateList"
            :key="item.vip_id"
          >
            <span class="grade">{{ $t('UI_VIP_Share.grade') }}{{ getVipCurrentLevel(item.vip_id) }}</span>
            <span class="percentage">{{ item.rate }}%</span>
          </div>
        </div>
      </div>
      <div class="loss_refund" v-else>
        <img class="refund_level_img" :src="getCacheIcon('vip_crown')" alt="" />
        <div class="refund_level_box">
          <span class="refund_level_text">{{ $t('UI_Loss_Return_Detail') }} &nbsp;</span>
        </div>
        <div class="refund_title">{{ lossReturnInfo?.title }}</div>
        <div class="refund_list">
          <div class="refund_item_title">
            <span class="grade">{{ $t('UI_Loss_Loss_Amount') }}</span>
            <span class="percentage">{{ lossReturnInfo?.tableText }}</span>
          </div>
          <div
            class="refund_item"
            :class="{ current: item.amount == currMoneyLevel }"
            v-for="item in returnList"
            :key="item.amount"
          >
            <span class="grade">{{ item.amount }}</span>
            <span class="percentage">{{ item.value }}{{ lossReturnInfo?.unit }}</span>
          </div>
        </div>
      </div>

      <loss-explain ref="lossHelperRef" :content="contentText" />
      <loss-record-list ref="lossRecordRef" :list="recordList" />
    </div>
  </v-container>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { getCacheIcon } from '@/assets/imgCache.js'
import loadIcon from '@/assets/icons'
import lossExplain from './ui/loss-explain.vue'
import lossRecordList from './ui/loss-record-list.vue'
import CountTo from 'vue-count-to'
import { getLossConfig, getLossReceive, getLossRecord } from '@/api/modules/lossreturn.js'
import PublishSubscribe from 'publish-subscribe-js'
import _common from '@style/views/lossreturn.modules.scss'
// 旧的iconfont没更新了
const recordSvg = `<svg t="1736323292232" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="6574" width="200" height="200"><path d="M492.032 864.256c16.896 0 31.232-5.632 45.568-16.896 11.264-11.264 16.896-25.6 16.896-42.496 0-16.896-5.632-31.232-16.896-45.568-11.264-11.264-25.6-16.896-45.568-16.896-16.896 0-31.232 5.632-45.568 16.896-11.264 11.264-16.896 25.6-16.896 45.568 0 16.896 5.632 31.232 16.896 42.496 14.336 8.704 28.672 16.896 45.568 16.896z m51.2-250.368c0-11.264 5.632-22.528 19.968-34.304 14.336-11.264 31.232-25.6 48.128-39.936 16.896-14.336 34.304-31.232 48.128-51.2 14.336-19.968 22.528-39.936 25.6-65.536 0-25.6 0-51.2-8.704-73.728-5.632-22.528-16.896-42.496-34.304-59.904-14.336-16.896-36.864-28.672-59.904-39.936-25.6-8.704-54.272-14.336-88.064-14.336-42.496 0-76.8 8.704-102.4 22.528-28.672 14.336-48.128 31.232-65.536 54.272-16.896 19.968-28.672 42.496-36.864 65.536-5.632 22.528-8.704 39.936-8.704 54.272 0 16.896 5.632 31.232 16.896 39.936s22.528 11.264 34.304 11.264c11.264 0 22.528-3.072 34.304-11.264 11.264-5.632 14.336-16.896 14.336-31.232 0-8.704 3.072-16.896 8.704-31.232 5.632-11.264 11.264-22.528 19.968-34.304 8.704-11.264 19.968-19.968 31.232-28.672 11.264-8.704 25.6-11.264 42.496-11.264 31.232 0 54.272 8.704 73.728 22.528 16.896 14.336 25.6 34.304 25.6 56.832 0 11.264-3.072 22.528-11.264 31.232-5.632 8.704-14.336 19.968-25.6 28.672s-22.528 16.896-34.304 28.672c-11.264 8.704-22.528 19.968-34.304 28.672-11.264 8.704-19.968 19.968-25.6 31.232-5.632 11.264-11.264 22.528-11.264 36.864v39.936c0 11.264 5.632 19.968 14.336 28.672 8.704 8.704 22.528 14.336 36.864 14.336s28.672-5.632 36.864-14.336c8.704-8.704 14.336-19.968 11.264-31.232V614.4l4.096-0.512z" fill="#FFFFFF" p-id="6575"></path><path d="M512 512m-454.656 0a454.656 454.656 0 1 0 909.312 0 454.656 454.656 0 1 0-909.312 0Z" fill="#F12C4C" p-id="6576"></path><path d="M512 276.992c-130.048 0-235.008 105.472-235.008 235.008 0 130.048 105.472 235.008 235.008 235.008s235.008-105.472 235.008-235.008c0-130.048-104.96-235.008-235.008-235.008z m-0.512 406.528c-95.232 0-172.544-77.312-172.544-172.544s77.312-172.544 172.544-172.544 172.544 77.312 172.544 172.544c-0.512 95.744-77.312 172.544-172.544 172.544z" fill="#FFFFFF" p-id="6577"></path><path d="M590.336 496.128h-62.464V433.664c0-17.408-13.824-31.232-31.232-31.232s-31.232 13.824-31.232 31.232v94.208c0 17.408 13.824 31.232 31.232 31.232h94.208c17.408 0 31.232-13.824 31.232-31.232s-14.336-31.744-31.744-31.744z" fill="#FFFFFF" p-id="6578"></path></svg>`
export default {
  name: 'lossReturn',
  components: {
    lossExplain,
    lossRecordList,
    CountTo
  },
  data() {
    return {
      lossInfo: {}, // 配置信息
      duration: 1000, // 计数倒计时(毫秒)
      isLossreActive: true, // 当前活动是否还在活动时间内
      // 倒计时数据
      lossreTimeData: {
        days: '00',
        hours: '00',
        minutes: '00',
        seconds: '00'
      },
      recordSvg,
      recordList: [] // 领取记录
    }
  },
  computed: {
    ...mapState('base', ['user', 'isApp', 'languageType', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    // 金额标识
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 活动说明
    contentText() {
      return this.lossInfo?.activity_info?.content || ''
    },
    // 返还类型 1:比例 2：固定
    lossReturnInfo() {
      const return_type = this.lossInfo?.activity_info?.return_type || 1
      return {
        title: return_type == 2 ? this.$t('UI_Loss_Refund_Amount') : this.$t('UI_Loss_Refund_Ratio_Text'),
        tableText: return_type == 2 ? this.$t('UI_Loss_Fixed_Amount') : this.$t('UI_Loss_Refund_Percentage'),
        unit: return_type == 2 ? '' : '%'
      }
    },
    // 返还最低下限
    minMoney() {
      const money = Math.max(0, Number(this.lossInfo?.activity_info?.gold_min || 0))
      return this.$u.formatNumber(money)
    },
    /**
     * 返还最高上限
     * gold_max -1:无上限
     */
    maxMoney() {
      return Number(this.lossInfo?.activity_info?.gold_max ?? -1)
    },
    // 返还金额比例
    returnRate() {
      if (Number(this.returnMoney) <= Number(this.minMoney)) return 0

      const moneyThan = Number(this.returnMoney) / (this.maxMoney == -1 ? 1000 : this.maxMoney)
      return moneyThan < 0 ? 0 : moneyThan > 1 ? 1 : moneyThan
    },
    // 奖励
    returnReward() {
      const { activity_user_info = {} } = this.lossInfo
      const { unreceive_gold = 0 } = activity_user_info
      return this.$u.formatNumber(unreceive_gold)
    },
    // 亏损返还档位列表
    returnList() {
      return Object.entries(this.lossInfo?.activity_info?.gold_range_config || {})
        .map(item => ({
          amount: item[0],
          value: item[1]
        }))
        ?.sort((a, b) => a.amount - b.amount)
    },
    // 当前金额档位，loss_amount小于最低档位为空串，大于最高档位为最高档位，处于100-200档位之间为100档位
    currMoneyLevel() {
      const loss_amount = this.lossInfo?.activity_user_info?.loss_amount || 0
      let level = ''
      for (const item of this.returnList) {
        if (loss_amount < item.amount) {
          return level
        }
        level = item.amount
      }
      return level
    },
    // 返还金额 return_type==1为固定金额，return_type==2为比例，净亏损金额 * 档位的返还比例
    returnMoney() {
      if (!this.isNewActivity) {
        const { vip_id = 1 } = this.user
        const { activity_info = {}, activity_user_info = {} } = this.lossInfo
        const { vip_rate_list = [] } = activity_info
        const { loss_amount = 0 } = activity_user_info

        const vipId = this.isKeepLevel ? 1 : vip_id
        const vipRate = vip_rate_list.find(item => item.vip_id == vipId) || {}
        const { rate = 0 } = vipRate
        const money = Number(loss_amount) * Number(rate / 100)

        return this.$u.formatNumber(money)
      }
      let money = this.lossInfo?.activity_info?.gold_range_config?.[this.currMoneyLevel] || 0 // 默认固定金额
      const return_type = this.lossInfo?.activity_info?.return_type || 1
      if (return_type === 1) {
        const loss_amount = this.lossInfo?.activity_user_info?.loss_amount || 0
        return ((loss_amount * money) / 100).toFixed(2)
      }
      return this.$u.formatNumber(money)
    },
    // 未领取奖励
    notReceiveGold() {
      const { activity_user_info = {} } = this.lossInfo
      const { not_receive_gold = 0 } = activity_user_info
      return not_receive_gold
    },
    isNewActivity() {
      return this.lossInfo?.activity_info?.version > 0
    },
    // 返还比例
    returnRateList() {
      const { activity_info = {} } = this.lossInfo
      const { vip_rate_list = [] } = activity_info
      return vip_rate_list
    }
  },
  watch: {
    returnRate(n) {
      this.updateProgress()
    },
    //语言切换刷新数据
    languageType() {
      this.getConfig()
    }
  },
  created() {
    window.removeEventListener('resize', this.onResize)
    window.addEventListener('resize', this.onResize)
    this.renderPage()
  },
  mounted() {
    this.getConfig()
    this.getRecord()
  },
  methods: {
    loadIcon: (key, bool = false) => loadIcon(key, bool),
    commonStyle: k => _common[k],
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    },
    // 获取配置
    async getConfig() {
      const params = { token: this.user?.token, language: this.languageType }
      const res = await getLossConfig(params).catch(e => e)
      if (!res || res.code != 0) return

      this.lossInfo = res.data && Object.prototype.toString.call(res.data) == '[object Object]' ? res.data : {}
      this.countDown()
    },
    // 获取记录
    async getRecord() {
      const params = { token: this.user?.token, language: this.languageType }
      const res = await getLossRecord(params).catch(e => e)
      if (!res || res.code != 0) return
      // 未匹配到对应的前端状态，就不需要给用户展示
      const list = (res.data.list || [])
        .filter(v => this.recordStatusState(v.settle_status))
        .map(item => {
          let time = `${this.$u.useTimestamp(item.period_start * 1000, 'yyyy-MM-dd')} ~ ${this.$u.useTimestamp(item.period_end * 1000, 'yyyy-MM-dd')}`
          if (this.isApp) {
            time = `${this.$u.useTimestamp(item.period_start * 1000, 'yyyy-MM-dd')}~<br/>${this.$u.useTimestamp(item.period_end * 1000, 'yyyy-MM-dd')}`
          }
          return {
            ...item,
            time,
            ...this.recordStatusState(item.settle_status)
          }
        })
      this.recordList = list
    },
    // 渲染界面
    renderPage(callback) {
      this.$nextTick(() => {
        const loss_wave = document.querySelectorAll('.loss_wave')[0]
        if (loss_wave) {
          const { width = 324 } = loss_wave.getBoundingClientRect()
          loss_wave.style.setProperty('--loss-wave-height', `${width}px`)
          const loss_wave_box = loss_wave.querySelectorAll('.loss_wave_box')[0]
          const loss_shadow = loss_wave.querySelectorAll('.loss_shadow')[0]
          if (loss_wave_box) {
            const { width = 264 } = loss_wave_box.getBoundingClientRect()
            loss_wave_box.style.setProperty('--loss-wave-box-height', `${width}px`)
            const loss_wave_bg = loss_wave_box.querySelectorAll('.loss_wave_bg')[0]
            if (loss_wave_bg) {
              const { width = 220 } = loss_wave_bg.getBoundingClientRect()
              loss_wave_bg.style.setProperty('--loss-wave-bg-height', `${width}px`)
              // 设置波浪盒子大小
              const loss_wave_bg_style = getComputedStyle(loss_wave_bg, false) || {}
              const borderW = parseInt(loss_wave_bg_style['border-width']) * 2
              const wave_box_w = (width - borderW) * 2.5
              const wave_box_h = (width - borderW) * 2.5
              loss_wave_bg.style.setProperty('--wave-box-w', `${wave_box_w}px`)
              loss_wave_bg.style.setProperty('--wave-box-h', `${wave_box_h}px`)
              loss_wave_bg.style.setProperty('--loss-wave-change-bottom', `-${wave_box_h}px`)
            }
          }
          if (loss_shadow) {
            const than = this.isApp ? 649 / 249 : 519 / 199
            const { width = 340 } = loss_shadow.getBoundingClientRect()
            loss_shadow.style.setProperty('--loss-shadow-height', `${width / than}px`)
          }

          if (typeof callback == 'function') callback()
        }
      })
    },
    // 监听屏幕变化界面重新渲染
    onResize() {
      this.renderPage(() => {
        this.updateProgress()
      })
    },
    // 更新进度渲染
    updateProgress() {
      this.$nextTick(() => {
        const loss_wave_box = document.querySelectorAll('.loss_wave_box')[0]
        const loss_wave_bg = loss_wave_box.querySelectorAll('.loss_wave_bg')[0]
        if (loss_wave_bg) {
          const loss_wave_bg_height = window
            .getComputedStyle(loss_wave_bg)
            .getPropertyValue('--loss-wave-bg-height')
            .trim()
          const loss_wave_bg_style = getComputedStyle(loss_wave_bg, false) || {}
          const borderW = parseInt(loss_wave_bg_style['border-width']) * 2

          const loss_wave_bg_h = parseInt(loss_wave_bg_height) - borderW
          const loss_wave_before_h = window.getComputedStyle(loss_wave_bg).getPropertyValue('--wave-box-h').trim()
          const min_b = parseInt(loss_wave_before_h)
          const max_b = parseInt(loss_wave_before_h) - parseInt(loss_wave_bg_h)
          const change_b = min_b - max_b

          // 需要波浪变化到的距离
          const wave_b = min_b - change_b * this.returnRate
          loss_wave_bg.style.setProperty('--loss-wave-change-bottom', `-${wave_b}px`)
        }
      })
    },
    // 领取奖励
    async onReceive() {
      if (Number(this.returnReward) <= 0) return

      const res = await getLossReceive({ token: this.user?.token }).catch(e => e)
      if (!res || res.code != 0) return

      const resData = res.data && Object.prototype.toString.call(res.data) == '[object Object]' ? res.data : {}

      this.getConfig()
      this.getRecord()
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'red_dot_bet_loss_gift',
        language: this.languageType,
        token: this.user.token
      })

      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('UI_Code_Bonus'),
          value: resData.reward_gold
        }
      })
    },
    // 详情弹窗
    onExplain() {
      if (this.$refs['lossHelperRef']) this.$refs['lossHelperRef'].show()
    },
    // 往期领取记录弹窗
    onRecord() {
      if (this.$refs['lossRecordRef']) this.$refs['lossRecordRef'].show()
    },
    // 倒计时
    countDown() {
      const { activity_info = {} } = this.lossInfo
      const { start_time = 0, end_time = 0 } = activity_info
      const curTime = Math.round(Date.now() / 1000)
      this.isLossreActive = curTime >= Number(start_time) && curTime <= Number(end_time)

      if (this.countDownTimer) {
        clearTimeout(this.countDownTimer)
        this.countDownTimer = null
      }
      if (this.isLossreActive) {
        const { day, hh, mm, ss } = this.$u.datetime.getDifferTime(curTime * 1000, end_time * 1000)
        this.lossreTimeData = { days: day, hours: hh, minutes: mm, seconds: ss }
      }
      this.countDownTimer = setTimeout(() => {
        if (!this.isLossreActive) return
        this.countDown()
      }, 1 * 1000)
    },
    recordStatusState(type) {
      /**
       * 0 未到账
       * 1 已到账
       * -1 手动到账
       * 3 已领取
       * 4 自动领取
       * 2 已失效
       * -2 已取消
       */
      const STATE = {
        // 后端status对应前端按钮状态
        3: {
          text: this.$t('UI_Loss_Status_Received'),
          color: '#41FFB9'
        }, // 已领取
        4: {
          text: this.$t('UI_Loss_Status_Auto_Collection'),
          color: '#41FFB9'
        }, // 自动领取
        2: {
          text: this.$t('UI_Loss_Status_Expired'),
          color: '#BBCEDF'
        }, // 已过期
        '-2': {
          text: this.$t('UI_Loss_Status_Refuse'),
          color: '#F12C4C'
        } // 已拒绝
      }
      return STATE[type]
    }
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.onResize)
    if (this.countDownTimer) {
      clearTimeout(this.countDownTimer)
      this.countDownTimer = null
    }
  }
}
</script>

